<template>
  <div class="login">
    <div class="fh">
    <img @click="goback" src="../../assets/fh.png" alt="">
    </div>
    <span></span>
    <div class="login_1">
      <input type="text" v-model="msg.uname" placeholder="">
      <input type="password" v-model="msg.upwd" placeholder="">
      
    </div>
    <mt-button size="large" type="danger" @click="logining"></mt-button>
    <div class="login_2">
      <span @click="register"></span>
      <span>|</span>
      <span></span>
    </div>
    <div class="login_3">
      <img src="../../assets/QQ.png" alt="">
      <img src="../../assets/wei-xin.png" alt="">
      <img src="../../assets/weibo.png" alt="">
      <img src="../../assets/youxiang.png" alt="">
    </div>
    <div class="login_4">
      <span>/</span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      msg:{
        uname:"",
        upwd:"",
      }
      
    }
  },
  methods:{
    logining(){
      var reg = /^[a-z0-9]{3,12}$/i;
      var u = this.uname;
      var p = this.upwd;
      if(this.msg.uname===''||this.msg.upwd===''){
        this.$toast("")
      }
      if(!reg.test(u)){
          this.$messagebox("","");
           return;//
           }
         //4:
           if(!reg.test(p)){
             this.$messagebox("","");
             return;//  
           }
           //5:url,
           var url = "login";
           //6:obj,
           var obj = {uname:u,upwd:p};
           //7:ajax
           this.axios.get(url,{params:obj}).then(res=>{
              //8:
              //9:-1  
              //10:1    /Product 
              if(res.data.code==-1){
                  this.$messagebox("","");
              }else{
                  this.$toast("");
                  this.$router.push("/cook");
              }
           })
    },
    goback(){
      this.$router.push("/cook");
    },
    register(){
      this.$router.push("/login/register");
    }
  }
}
</script>
<style scoped>
.fh{
  text-align: start;
}
.fh>img{
  width:25px;
}
.login{
  background-color: #fff;
  padding: 20px;
  text-align: center;
}
.login>span{
  font-weight: bold;
  font-size: 30px;
}
.login_1{
  margin-top: 50px;
  padding-bottom: 50px;
}
input{
    background-color: #fff;
    border-radius: 0;
    border-bottom: 1px solid #ccc;
    width: 89%;
    height: 40px;
    padding-left: 40px;
    margin-top: 10px;
    
  }

  .login_1>input:first-child{
    background-image: url("../../assets/logining.png");
  }
  .login_1>input:last-child{
    background-image: url("../../assets/login_pass.png");
  }
  .login_2{
    margin-top: 20px;
    color: #aaa;
  }
  .login_2>span{
  padding-right: 5px;
  }
  .login_3{
    margin-top:300px;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .login_3>img{
    width: 60px;
  }
  .login_4{
    margin-top: 20px;
  }
  .login_4>span{
    font-size: 14px;
  }
  .login_4>span:first-child,.login_4>span:nth-child(3){
    color: #aaa;
  }
</style>